import Vue from 'vue'
import Router from 'vue-router'
import Landing from '@/components/landing/Landing'
import MakingPassport from '@/components/MakingPassport'
import SignIn from '@/components/SignIn'
import MyPassport from '@/components/MyPassport'
import FindPassport from '@/components/FindPassport'
import MeetUp from '@/components/Meetup'
Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Landing',
      component: Landing
    },
    {
      path: '/signup',
      name: 'Sign up',
      component: MakingPassport
    },
    {
      path: '/signin',
      name: 'Sign in',
      component: SignIn
    },
    {
      path: '/mypassport',
      name: 'My passport',
      component: MyPassport
    },
    {
      path: '/findpassport',
      name: 'Find passport',
      component: FindPassport
    },
    {
      path: '/meetup',
      name: 'Meet up',
      component: MeetUp
    }
  ]
})
