<script>
import { VueEditor } from 'vue2-editor'
import Passport from '@/components/passport/Passport'
export default {
  name: 'Find Passport',
  components: {
    VueEditor,
    Passport
  },
  watch: {
    loader () {
      const l = this.loader
      this[l] = !this[l]
      setTimeout(() => (this[l] = false), 3000)
      this.loader = null
    }
  },
  data () {
    return {
      content: '<h3>Initial Content</h3>'
    }
  }
}
</script>
