<script>
import MixInVueEditor from '@/mixin/VueEditor'
import Passport from '@/components/passport/Passport'
export default {
  mixins: [MixInVueEditor],
  components: {
    Passport
  },
  name: 'MyPassport',
  watch: {
    loader () {
      const l = this.loader
      this[l] = !this[l]
      setTimeout(() => (this[l] = false), 3000)
      this.loader = null
    }
  },
  data () {
    return {
    }
  }
}
</script>
